import { create as createXML } from 'xmlbuilder';
import 'promise-polyfill/src/polyfill';
import {post as httpPost } from 'superagent';

// Respond to message from parent thread
self.addEventListener('message', (event) => {
    const eventData = event.data || {};
    const requestBody = eventData.contentType === 'text/xml' ? buildXMLData(eventData.data) : eventData.data().toString();

    httpPost(eventData.url)
        .set('Content-Type', eventData.contentType)
        .send(requestBody.toString())
        .then(response => self.postMessage({response : JSON.stringify(response.status)}))
        .catch(error => self.postMessage({error : error.message}));
});

function buildXMLData(data) {
    return createXML(data).end({pretty:true});
}
